<template>
  <div class="page">
    <!-- 页头 -->
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="手机充值"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content withdraw-phone">

      <div class="phone-number">
        <div class="title">手机号</div>
        <label>
          <input type="tel" @keyup="telInput($event)" maxlength="13" v-model="form.mobile" placeholder="输入手机号码">
        </label>
      </div>

      <withdraw-options :options="mobileInfo.phoneOptions" :selected-index="selectedIndex" :on-select="onSelect"/>

      <div class="tips-content">
        <span v-show="selected">消耗咖币 <span class="assertive fontSemibold">{{mobileInfo.realprices[selectedIndex]}}</span>，</span><span>当前剩余咖币 <span class="assertive fontSemibold">{{info.balance}}</span></span>
        <div class="tips">注：赠送筹码仅限每天首次成功提现</div>
      </div>

      <button :disabled="buttonDisabled" :class="{'button-active': !buttonDisabled}" class="button button-withdraw" @click="submit()">
        充值
      </button>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins";

  .withdraw-phone {
    margin-top: 50px;
    background: #FFF;
    padding: 25px 30px 25px 30px;

    .phone-number {
      width: 200px;
      border-bottom: 1px solid #ddd;
      padding-bottom: 3px;

      .title {
        font-size: 12px;
        color: #B5B5B5;
        line-height: 17px;
        height: 17px;

        @include font-regular();
      }

      label {
        display: block;
        height: 33px;
        width: 100%;

        input {
          height: 33px;
          line-height: 33px;
          width: 100%;
          display: inline-block;
          font-size: 24px;
          color: #3A3A3A;
          padding: 0;

          @include font-regular();

          &::-webkit-input-placeholder {
            font-size: 24px;
            color: #DDDDDD;
            line-height: 33px;
            height: 33px;

            @include font-regular();
          }
        }
      }
    }

    .tips-content {
      height: 41px;
      font-size: 13px;
      color: #484746;

      @include font-regular();

      .tips {
        margin-top: 5px;
        line-height: 18px;
      }

      .fontSemibold {
        @include font-semibold();
      }
    }

    .button-withdraw {
      width: 172px;
      height: 40px;
      min-height: 40px;
      border-radius: 2px;
      line-height: 40px;
      margin-top: 30px;
      font-size: 13px;
      color: #FFFFFF;
      background-color: #dddddd;

      @include font-semibold();
    }

    .button.disabled, .button[disabled] {
      opacity: 1;
    }

    .button-active {
      background: #EF4F2A;
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import withdrawOptions from './WithdrawOptions.vue'
  import {
    popup_wechat_bind,
    popup_user_freeze,
    popup_confirm_phone
  } from '../popup.js'
  import {mapState, mapActions, mapGetters} from 'vuex'

//  let phoneOptions = [
//    { title: '充10元', desc: '' },
//    { title: '充30元', desc: '送筹码1枚' },
//    { title: '充50元', desc: '送筹码2枚' },
//    { title: '充100元', desc: '送筹码5枚' }
//  ]

  export default {
    components: {
      withdrawOptions,
      AnimateHeader
    },

    data () {
      return {
        form: {
          mobile: '',
          price: ''
        },
        selected: true,
        selectedIndex: 0
      }
    },

    watch: {
      'form.mobile' (val, oldVal) {
//        let oldL = oldVal.length
//        var text_len = val.length
//        if (oldL > text_len) {
//          return
//        }
//        if (text_len === 3 || text_len === 8) {
//          val += ' '
//        } else if (text_len === 11) {
//          val = val.replace(/(\d{3})(\d{4})(\d{4})$/, '$1 $2 $3')
//        }
        val = val.replace(/(\d{3})(\d{4})(\d{4})$/, '$1 $2 $3')
        this.form.mobile = val
      }
    },

    computed: {
      ...mapState('withdraw', [
        'info'
      ]),

      ...mapGetters('withdraw', [
        'mobileInfo'
      ]),

      buttonDisabled () {
        if (this.form.mobile !== '') {
          return false
        }
        return true
      }
    },

    created () {
      this.fetch()
    },

    methods: {
      telInput (e) {
        let start = e.target.selectionStart
        let val = this.form.mobile
        // arrow keyboard keypress
        if (e.which === 37 || e.which === 39) {
          let isSpecialStart = (start === 3 || start === 8)
          if (e.which === 37 && isSpecialStart) {
            start--
          }
          if (e.which === 39 && isSpecialStart) {
            start++
          }
          if (isSpecialStart) {
            this.$nextTick(() => {
              e.target.selectionStart = start
              e.target.selectionEnd = start
            })
          }
          return
        }

        // delete
        if ((e.which === 8 || e.keyCode === 8) && (start === 8 || start === 3)) {
          val = val.substr(0, start - 1) + val.substr(start)
          start--
        }
        // input
        if (start === 3 || start === 8) {
          val = val.substr(0, start) + ' ' + val.substr(start)
          start++
        } else if (val.length === 11) {
          val = val.replace(/(\d{3})(\d{4})(\d{4})$/, '$1 $2 $3')
        }
        this.form.mobile = val
        this.$nextTick(() => {
          e.target.selectionStart = start
          e.target.selectionEnd = start
        })
      },

      fetch () {
        this.getWithdrawList().then((payload) => {
          this.form.mobile = this.mobileInfo.mobile
        })
      },

      ...mapActions('withdraw', [
        'getWithdrawList',
        'submitMobile'
      ]),

      back () {
        $router.back('/withdraw')
      },

      onSelect (index) {
        this.selectedIndex = index
        this.selected = true
      },

      // 格式化手机号： xxx xxxx xxxx
      formatPhone (phone) {
        let reg = /^(\d{3})(\d{4})(\d{4})$/
        let matches = reg.exec(phone)
        return matches[1] + ' ' + matches[2] + ' ' + matches[3]
      },

      submit () {
        if (this.submitLock) return
        this.submitLock = true
        setTimeout(() => { this.submitLock = false }, 1000)
        if (this.info.mobile.enabled === 0) { // 需要绑定微信
          let p = popup_wechat_bind
          let popup = $popup.fromTemplate(p.template, p.options)
          popup.show().then(btn => {
            if (btn === 0) { // 取消
            } else if (btn === 1) { // 前往微信绑定页面
              $router.forward({
                path: '/user/bindWeixin',
                query: { backroute: '/withdraw/phone', jumproute: '/withdraw/phone' }
              })
            }
          })
          return
        }
        let mobile = this.form.mobile.replace(/\s/g, '')
        let price = this.mobileInfo.amount[this.selectedIndex]
        if (!/^1[3456789]\d{9}$/.test(mobile)) {
          $toast.show('请输入正确的手机号！')
          return
        }
        let pp = popup_confirm_phone
//        let phoneFormat = this.formatPhone(this.form.mobile)
        let popup = $popup.fromTemplate(pp.template(this.form.mobile), pp.options)
        let sec = 3
        popup.updateButtonTextByIndex(1, '确认（3s）')

        let countDownTimer = setInterval(() => {
          if (sec > 1) {
            popup.updateButtonTextByIndex(1, `确认（${--sec}s）`)
          } else {
            clearInterval(countDownTimer)
            popup.updateButtonTextByIndex(1, '确认')
            popup.updateButtonStatusByIndex(1, true)
          }
        }, 1000)

        popup.show().then((btn) => {
          clearInterval(countDownTimer)

          if (btn === 0) { // 取消
            return
          } else if (btn === 1) {
            this.submitMobile({mobile, price}).then((res) => {
              if (res.status === 200) {
                if (res.data.err_code === 90000) {
                  let popup = $popup.fromTemplate(
                    popup_user_freeze.template(),
                    popup_user_freeze.options
                  )
                  popup.show()
                } else {
                  $router.forward({
                    path: '/withdraw/result/phone'
                  })
                }
              }
            })
          }
        })

        setTimeout(() => { popup.updateButtonStatusByIndex(1, false) }, 0)
      }
    }
  }
</script>



